.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-color: #904dcf;
}

.board {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  width: 427px;
  height: 425px;
  padding-bottom: 15px;
  background-color: #b4348c;
  border-radius: 30px;
  border: 20px solid #e5aad5;
  gap: 0;
  box-sizing: border-box;
  margin-bottom: 5px;
}

.board-row {
  display: contents;
}

.tile {
  background-color: #eea6db;
  border: 7px solid #4c1939;
  border-radius: 20px;
  font-size: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-sizing: border-box;
  margin-bottom: 1px;
  width: 95px;
  height: 95px;
  transition: background 0.3s, box-shadow 0.3s, transform 0.3s;
}

.tile:hover {
  background: #dc1e5f;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transform: scale(1.05);
}

.tile.empty {
  background-color: transparent;
  pointer-events: none;
  cursor: not-allowed;
  border-width: 5px;
}

.tile-text {
  text-align: center;
  font-family: "Comic Sans MS";
  font-weight: bolder;
}

.button-shuffle {
  background-color: #328632;
  border: 5px solid #e4d6c3;
  border-radius: 20px;
  font-size: 40px;
  color: white;
  font-weight: bolder;
  height: 60px;
  width: 415px;
  cursor: pointer;
  transition: background 0.3s, box-shadow 0.3s, transform 0.3s;
}

.button-shuffle:hover {
  background-color: #4fd93a;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transform: scale(1.05);
}

.heading {
  text-align: center;
  font-size: 60px;
  color: white;
  font-family: "Comic Sans MS", cursive;
  font-weight: bolder;
  margin-top: 5px;
  letter-spacing: 1.1rem;
  border-radius: 20px;
  margin-bottom: 15px;
}

.game-info {
  display: flex;
  width: 430px;
  margin-bottom: 2px;
  margin-left: 7px;
  font-weight: bolder;
  padding: 0;
}

.info-box {
  background-color: #e5aad5;
  border: 2px solid #89426c;
  border-radius: 30px;
  padding: 10px 20px;
  font-size: 25px;
  font-weight: bolder;
  color: #4c1939;
  text-align: center;
  margin-right: 5px;
  margin-left: 5px;
  position: relative;
  width: 170px;
}

.info-box2 {
  background-color: #e5aad5;
  border: 2px solid #89426c;
  border-radius: 30px;
  padding: 10px 10px;
  font-size: 21px;
  font-weight: bolder;
  color: #4c1939;
  text-align: center;
  margin-right: 10px;
  position: relative;
  width: 250px;
}

.button-pause {
  background-color: #328632;
  border: none;
  border-radius: 50px;
  color: white;
  font-size: 16px;
  font-weight: bold;
  padding: 5px 10px;
  cursor: pointer;
  margin-left: 10px;
  transition: background 0.3s, transform 0.3s;
  width: 84px;
  height: 40px;
}

.button-pause:hover {
  background-color: #4fd93a;
  transform: scale(1.05);
}

.congratulations {
  position: absolute;
  margin-top: 5px;
  right: 55px;
  background-color: #4caf50;
  color: white;
  padding: 15px;
  border-radius: 50px;
  font-size: 37px;
  font-family: "Comic Sans MS", cursive;
  font-weight: bolder;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.tile.disabled {
  pointer-events: none;
  opacity: 0.5;
  cursor: not-allowed;
}